				"id": "/Users/seth/src/posm/posm.github.io/src/pages/getting-a-posm/index.md absPath of file >>> MarkdownRemark",
				"html": "<p>Interested in getting a POSM? This involves a little bit of DIY, but advanced tech skills are <strong>not</strong> required. This site has instructions that walk through the whole process.</p>\n<p>First, you order the hardware kit (you’ll have to assemble a few things). Next, you load the core software onto the device for a first-time install. This transforms the unit into a POSM. After that, the POSM is ready to go and can be used over and over again.</p>\n<h2>Anatomy of a POSM</h2>\n<h3>Hardware</h3>\n<p>We used the following hardware components for the original version of POSM, which cost about $300 in total:</p>\n<ul>\n<li><a href=\"http://smile.amazon.com/gp/product/B00XPVQHDU\">NUC Kit - BOXNUC5PPYH</a> - 2.6\nGHz quad core Pentium (N3700, 6W TDP). Includes a basic case.</li>\n<li><a href=\"http://smile.amazon.com/gp/product/B00OAJ412U\">Samsung 850 EVO 250GB</a></li>\n<li><a href=\"http://smile.amazon.com/gp/product/B00KQCOTCM\">8GB 1600MHz DDR3L CL9 SODIMM</a></li>\n<li><a href=\"https://www.thinkpenguin.com/gnu-linux/wireless-n-dual-band-bluetooth-40-m2-ngff-combo-card\">Wireless N Dual Band + Bluetooth 4.0 M.2 NGFF Combo Card</a> This is needed if you plan to have more than 14 devices connected at a time. Replace the stock wireless card with this.</li>\n</ul>\n<p>If you want to be able to use the OpenDroneMap feature in POSM, then you will want a slightly more souped-up set up hardware with greater processign power. Instead of the NUC unit above, order the following, somewhat more expensive model:</p>\n<ul>\n<li><a href=\"https://www.google.com/search?q=nuc+intel&#x26;oq=nuc+intel&#x26;aqs=chrome..69i57j0l5.1791j0j7&#x26;sourceid=chrome&#x26;ie=UTF-8#tbm=shop&#x26;q=Intel+Next+Unit+of+Computing+Kit+NUC6i7KYK+Barebone+Computer&#x26;spd=5256604869261291450\">NUC kit - BUC6i7KYK</a></li>\n</ul>\n<h3>Software</h3>\n<p>Once you’ve ordered and assembled the components, you’ll load the device with a core software package that transforms it into a POSM. The package includes the core POSM software, all of the tools that POSM supports, and other downloads/apps to support mapping and mobile data collection.</p>\n<p>The POSM download is <a href=\"http://posm.s3.amazonaws.com/releases/posm-0.6.1.iso\">here</a> and instructions are in the following section.</p>\n<p>For developers: the POSM software is free, open-source, and available through the <a href=\"https://github.com/AmericanRedCross/posm\">American Red Cross’ GitHub repository</a> with technical documentation and a variety of other resources. If you want more technical info or a less GUI-oriented means of setting this up, then head over to GitHub.</p>\n<h3>Other things you will need</h3>\n<p>The POSM hardware is basically a mini-computer. To set it up for the very first time, you’ll need to plug in the following:</p>\n<ul>\n<li>USB keyboard</li>\n<li>external monitor</li>\n<li>USB stick (minimum 4 GB)</li>\n</ul>\n<p>You’ll also need an internet connection with an ethernet cable to load an area of interest onto the POSM and set it up for the field.</p>\n<h2>First-time install</h2>\n<p>First, download the <a href=\"http://posm.s3.amazonaws.com/releases/posm-0.6.1.iso\">core POSM software</a>. This will download with the file name <code>posm-0.6.1.iso</code>. You need to move that onto a USB stick and extract it into a set of folders, but first you’ll have to change the USB formatting.</p>\n<h3>Configuring the USB stick</h3>\n<h4>Mac</h4>\n<p>On a Mac, plug in your USB stick and open Disk Utility. Navigate to the USB stick and click “Erase”.</p>\n<p>Give the USB stick a name. Set the format to <code>MS-DOS (FAT)</code> and the scheme to <code>GUID Partition Map</code>. Then click “Erase”, which will delete all existing USB contents and reformat the drive to make it compatible with the POSM. See below:</p>\n<p><img src=\"/guide/getting-a-posm/format.png\"></p>\n<h4>Windows</h4>\n<p>On a PC, connect the USB drive, then go to “Computer” or “My Computer”, right-click the drive and select “Format…” from the menu. Set the drive to FAT.</p>\n<h3>Moving and extracting the POSM download (AKA “ISO file”) onto the USB stick</h3>\n<p>If you don’t use command line, then moving the POSM download bundle involves downloading a file extractor to extract it (we use <a href=\"https://itunes.apple.com/us/app/the-unarchiver/id425424353?mt=12\">The Unarchiver</a> for Mac or <a href=\"http://www.7-zip.org/\">7-Zip</a> for Windows).</p>\n<p>On a Mac, open The Unarchiver and navigate to the <code>Extraction</code> tab. Where it says “Create a new folder for the extracted files”, select <code>Never</code>. The reason we do this is because the POSM download has to be extracted and moved onto the USB stick. Most file extractors will place extracted files into an overall folder. The POSM unit can’t handle this, and you can’t just manually move everything one level up because there are hidden files that will get missed.</p>\n<p><img src=\"/guide/getting-a-posm/extraction.png\"></p>\n<p>Use your extraction client (The Unarchiver or 7-Zip) to extract the download (<code>posm-0.6.1.iso</code>) onto the USB stick… again, making sure that the files do not end up in an overall folder. On a Mac, you do this by right-clicking the <code>posm-0.6.1.iso</code> file and selecting <code>Open with...</code> … <code>The Unarchiver</code>. Set the destination folder to the USB stick and press “Extract”.</p>\n<p>The contents of your USB stick should look like this:</p>\n<p><img src=\"/guide/getting-a-posm/finder.png\"></p>\n<h3>POSM first-time install</h3>\n<p>Alright, you’re finally ready. Take the Intel NUC unit that you’ve assembled and plug in an external monitor, a keyboard, and the USB stick you’ve prepared. Turn the power on.</p>\n<p>You’ll see a boot screen appear on your monitor. Press <code>F10</code> when prompted. You’ll have to be quick on the draw with this because the prompt only lasts a few seconds. If you miss it, then just turn the power off and try again.</p>\n<p>[need: prompt screen image]</p>\n<p>This will open a menu (see below) where you have to select which device to boot from. Use the arrow keys to select your USB stick, then hit enter.</p>\n<p><img src=\"/guide/getting-a-posm/boot.jpg\"></p>\n<p>[need: better version of this photo]</p>\n<p>That’s it. The software will install onto the NUC unit, transforming it into a POSM. This takes a few minutes. When it’s complete, you’ll see the following screen and you can safely power down the POSM:</p>\n<p>[need: complete screen image]</p>\n<p>Note: if you ever want to wipe and reinstall the core POSM software (to install a new version, etc), then it’s the same process - prep the USB stick, plug in the monitor etc, and press <code>F10</code> when prompted. The NUC unit will overwrite the existing software with the new download.</p>",
